import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule, Routes } from '@angular/router';

import { DjsComponent } from './djs.component';
import { DjComponent } from './dj/dj.component';
import { DjMediaComponent } from './dj-media/dj-media.component';
import { DjAboutComponent } from './dj-about/dj-about.component';
import { DjEditComponent } from './dj-edit/dj-edit.component';
import { DjAllEditComponent } from './dj-edit/all-edit/dj-all-edit.component';
import { DjMediaEditComponent } from './dj-edit/media-edit/dj-media-edit.component';
import { DjBasicEditComponent } from './dj-edit/basic-edit/dj-basic-edit.component';

const djsRoutes: Routes = [

  { path: "djs", component: DjsComponent, /* resolve: { halls: HallResolver }, */
    children: [
      {
        path: ":id", component: DjComponent, children: [
          {
             path: "edit", component: DjEditComponent,
             children: [
              { path: "all", component: DjAllEditComponent },
              { path: "gallery", component: DjMediaEditComponent },
              { path: "basic", component: DjBasicEditComponent }
             ]
        
          },
          { path: "media", component: DjMediaComponent },
          { path: 'about', component: DjAboutComponent }
        ]}
    ],
  }

];


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(djsRoutes)
  ],
  exports: [RouterModule]
})
export class DjsRoutingModule { }
