import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule, Routes } from '@angular/router';

import { FireworksComponent } from './fireworks.component';
import { FireworkComponent } from './firework/firework.component';
import { FireworkMediaComponent } from './firework-media/firework-media.component';
import { FireworkAboutComponent } from './firework-about/firework-about.component';
import { FireworkEditComponent } from './firework-edit/firework-edit.component';
import { FireworkAllEditComponent } from './firework-edit/all-edit/firework-all-edit.component';
import { FireworkBasicEditComponent } from './firework-edit/basic-edit/firework-basic-edit.component';
import { FireworkMediaEditComponent } from './firework-edit/media-edit/firework-media-edit.component';

const fireworksRoutes: Routes = [

  { path: "fireworks", component: FireworksComponent, /* resolve: { halls: HallResolver }, */
    children: [
      {
        path: ":id", component: FireworkComponent, children: [
          { path: "media", component: FireworkMediaComponent },
          { path: 'about', component: FireworkAboutComponent },
          {
             path: "edit", component: FireworkEditComponent ,
             children: [
              { path: "all", component: FireworkAllEditComponent },
              { path: "gallery", component: FireworkMediaEditComponent },
              { path: "basic", component: FireworkBasicEditComponent }
             ]
          },
        ]}
    ],
  }

];


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(fireworksRoutes)
  ],
  exports: [RouterModule]
})
export class FireworksRoutingModule { }
