import { Component, OnInit } from '@angular/core';

import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
import { HttpService } from '../../http.service';
import { first } from 'rxjs/operators';
// import { of } from 'rxjs';
// import { filter, tap, first } from 'rxjs/operators';
declare let $:any;


@Component({
  selector: 'app-log-in',
  templateUrl: './log-in.component.html',
  styleUrls: ['./log-in.component.css']
})
export class LogInComponent implements OnInit {

  logInform: FormGroup;
  phoneNum: any = '^((?=(02|03|04|08|09))[0-9]{2}[0-9]{3}[0-9]{4}|(?=(05|170|180))[0-9]{3}[0-9]{3}[0-9]{4})';
  emailPatt: string = '^[a-z]+[a-zA-Z_\\d]*@[A-Za-z]{2,10}\.[A-Za-z]{2,3}(?:\.?[a-z]{2})?$';
  passwordPatt: string = '^\\w{6,}$';
  isTrue = true;
  model;

   constructor(private router: Router, private route: ActivatedRoute, private http: HttpService) {}

  ngOnInit() {

    this.http.isAuthenticeted().pipe(first()).subscribe(
      (res) => {
        if(!res){
          this.model = $('#myFormModel').modal();
          this.formInit();
        }else{
          this.router.navigate(['/halls_events']);
        }
      }
    );
  }
  
  onSubmit() {
    
    if (this.logInform.valid) {
      this.isTrue = false;
      this.http.logIn(this.logInform.value).
        subscribe(evt => {

          
          let IntendedUri = this.http.IntendedUri? this.http.IntendedUri: "/";
          console.log(IntendedUri);
          this.router.navigate([IntendedUri]);
          $('.close').click();

        },(err) => {
          this.isTrue = true;
          console.log(err);
        });

    }
  }

  register(){
    $('.close').click(); 
    
    this.router.navigate(['/register']);
  }
  
  get f() { return this.logInform.controls; }

  private formInit(){
    this.logInform = new FormGroup({
      "name": new FormControl(null),
      'logInEmail': new FormControl(null),
      'password': new FormControl(null)
    });

    
    let thiz = this;
    this.model.on('hidden.bs.modal', function(e){
    
    thiz.router.navigate(["/"]);
    return e.preventDefault() ;
    });
  }
}
