import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule, Routes } from '@angular/router';

import { CarRentsComponent } from './car-rents.component';
import { CarRentComponent } from './car-rent/car-rent.component';
import { CarRentMediaComponent } from './car-rent-media/car-rent-media.component';
import { CarRentAboutComponent } from './car-rent-about/car-rent-about.component';
import { CarRentEditComponent } from './car-rent-edit/car-rent-edit.component';
import { CarRentAllEditComponent } from './car-rent-edit/all-edit/car-rent-all-edit.component';
import { CarRentBasicEditComponent } from './car-rent-edit/basic-edit/car-rent-basic-edit.component';

const carRentsRoutes: Routes = [

  { path: "car-rents", component: CarRentsComponent, /* resolve: { halls: HallResolver }, */
    children: [
      {
        path: ":id", component: CarRentComponent, children: [
          { path: "media", component: CarRentMediaComponent },
          { path: 'about', component: CarRentAboutComponent },
          {
             path: "edit", component: CarRentEditComponent ,
             children: [
              { path: "all", component: CarRentAllEditComponent },
              { path: "gallery", component: CarRentMediaComponent },
              { path: "basic", component: CarRentBasicEditComponent }
             ]
          },
        ]}
    ],
  }

];


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(carRentsRoutes)
  ],
  exports: [RouterModule]
})
export class CarRentsRoutingModule { }
