import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';

import { PhotographersComponent } from './photographers.component';
import { PhotographerComponent } from './photographer/photographer.component';
import { PhotographerMediaComponent } from './photographer-media/photographer-media.component';
import { PhotographerAboutComponent } from './photographer-about/photographer-about.component';
import { PhotographerEditComponent } from './photographer-edit/photographer-edit.component';
import { PhotoAllEditComponent } from './photographer-edit/all-edit/photo-all-edit.component';
import { PhotoMediaEditComponent } from './photographer-edit/media-edit/photo-media-edit.component';
import { PhotoBasicEditComponent } from './photographer-edit/basic-edit/photo-basic-edit.component';
import { PhotographersResolver } from './photographers-resolver.service';


const photographersRouting: Routes = [

  { path: "photographers", component: PhotographersComponent, resolve: { photographers: PhotographersResolver},
    children: [
      {
        path: ":id", component: PhotographerComponent, children: [
          
          { path: "media", component: PhotographerMediaComponent },
          { path: 'about', component: PhotographerAboutComponent },
          {
             path: "edit", component: PhotographerEditComponent, /* canActivate: [RouteGuardService] , */
             children: [
              { path: "all", component: PhotoAllEditComponent/* , canDeactivate: [CanDeactivateGuardService] */ },
              { path: "gallery", component: PhotoMediaEditComponent/* , canDeactivate: [CanDeactivateGuardService] */ },
              { path: "basic", component: PhotoBasicEditComponent/* , canDeactivate: [CanDeactivateGuardService] */ }
             ]
          },
        ]}
    ],
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(photographersRouting)
  ],
  exports: [RouterModule]
})
export class PhotographersRoutingModule { }
