import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule, Routes } from '@angular/router';

import { HotelsComponent } from './hotels.component';
import { HotelComponent } from './hotel/hotel.component';
import { HotelMediaComponent } from './hotel-media/hotel-media.component';
import { HotelAboutComponent } from './hotel-about/hotel-about.component';
import { HotelEditComponent } from './hotel-edit/hotel-edit.component';
import { HotelAllEditComponent } from './hotel-edit/all-edit/hotel-all-edit.component';
import { HotelBasicEditComponent } from './hotel-edit/basic-edit/hotel-basic-edit.component';
import { HotelMediaEditComponent } from './hotel-edit/media-edit/hotel-media-edit.component';

const hotelsRoutes: Routes = [

  { path: "hotels", component: HotelsComponent, /* resolve: { halls: HallResolver }, */
    children: [
      {
        path: ":id", component: HotelComponent, children: [
          { path: "media", component: HotelMediaComponent },
          { path: 'about', component: HotelAboutComponent },
          {
             path: "edit", component: HotelEditComponent ,
             children: [
              { path: "all", component: HotelAllEditComponent },
              { path: "gallery", component: HotelMediaEditComponent },
              { path: "basic", component: HotelBasicEditComponent }
             ]
          },
        ]}
    ],
  }

];


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(hotelsRoutes)
  ],
  exports: [RouterModule]
})
export class HotelsRoutingModule { }
