  customerProps: Observable<any>;
  costtumerSubscriber: Subscription;
  private user;
  isTrue: Observable<any> | boolean;


  constructor(private halls: CustomersDataService, private router: Router, private route: ActivatedRoute, private http: HttpService) { }

  ngOnInit() {

    // let pathUrl = this.route.url["value"][0].path;this.route.snapshot.params['id']
    let pathUrl = this.route.snapshot.params['id'];
    this.user = this.http.authUser;
    
    this.http.userObs.subscribe((logged) => { this.checkCustomer(pathUrl,logged); });
  }

  checkCustomer(uri,authUser){
    this.halls.getById(uri).pipe(
      first(param => (typeof param === "object") || param === 1))
      .subscribe(
      (cost)=> {
        // console.log(cost);
        let co = cost['customer'];
        if(co && co['id']){
          let customerEmail = co["email"];
          let authEmail = authUser? authUser['email']: false;
          
          if(customerEmail) this.customerProps = of(co);
          
          if (customerEmail === authEmail) {
            this.http.authUser = authUser;
            // this.halls.customerEmit(co);
            this.isTrue = of(true);
          } else {
            this.isTrue = of(false);
          } 

        }else{
          setTimeout(() => {
            let goTo = this.router.url.split(uri)[0];
            this.router.navigate([goTo]);
          },100);
        }
    });
  }

