import { Component, OnInit } from '@angular/core';
import {  Router, ActivatedRoute } from '@angular/router';
import { Observable, of } from 'rxjs';
import { HallType } from '../../../../../customers/hall-type';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { CustomersDataService } from '../../../../../customers/customers-data-service';
import { HttpHeaders } from '@angular/common/http';
import { HttpService } from '../../../../../http.service';
import { find, tap, map, first } from 'rxjs/operators';
import { CanDeactivateComponent } from '../../../../../can-deactivate-guard.service';
import { FormProccesorService } from '../../../../../customers/form-proccesor.service';
//  declare let $:any;

@Component({
  selector: 'app-photo-basic-edit',
  templateUrl: './photo-basic-edit.component.html',
  styleUrls: ['./photo-basic-edit.component.css']
})
export class PhotoBasicEditComponent implements OnInit {

  phoneNum: any = '^((?=(02|03|04|08|09))[0-9]{2}[0-9]{3}[0-9]{4}|(?=(05|170|180))[0-9]{3}[0-9]{3}[0-9]{4})';
  emailPatteren: string = '^[a-z]+[a-zA-Z_\\d]*@[A-Za-z]{2,10}\.[A-Za-z]{2,3}(?:\.?[a-z]{2})?$';
  /***************  ********************/
  customer: HallType;
  
  /* **************************** */
  private apiKey;
  addCustomerForm: FormGroup;

  isTrue: Observable<boolean>;
  masseges:{} = {};

  constructor(private router: Router, 
      private halls: CustomersDataService, 
      private http: HttpService,
      private valForm: FormProccesorService) { }

  ngOnInit() {

    this.halls.customerObsever.pipe(
      first((val) =>  {return val['customer']['id']})
      ).subscribe(cost => {
    
      let cId = (cost && cost["user_id"]) ? cost["user_id"] : false;
      let authUser:any = this.http.authUser;
      let uId = authUser? authUser["id"] : false;

      console.log('customerId: '+ cId + " userId "+ uId);
      if(cId === uId){
        this.customer = cost;
        this.apiKey = this.http.getApiKey();
        this.formInt();
        this.isTrue = of(true);
      }else{
        // let state = decodeURIComponent(this.router.url).split("/");
        // let media = "/"+state[1]+"/"+cost["company"]+"/media";
        this.isTrue = of(false);
        // console.log(media);
        // this.router.navigate([media]);
      }

    });
  }

  update(customer){
    
    let comp = customer['id'];
    let controls = this.addCustomerForm.controls[comp];
    let items = this.valForm.validate(controls,this.customer,comp);
    
    this.masseges = items['errors'];
    if(items['status']) this.send(items['success']);
  }

  inputReset(customer){
    let comp = customer['id'];
    this.addCustomerForm.controls[comp].reset();
  }

  default(customer){
    let comp = customer.id;
    this.addCustomerForm.controls[comp].setValue(this.customer[comp])
  }

  canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
    console.log("canDeactivate called!");
    if(this.addCustomerForm.dirty || this.addCustomerForm.touched){
      return confirm("לא שמרתה את הפרטים. תרצה לעזוב דף זה בכל זאת?")
    }else{
      return true;
    }
  }

  textAreaAdjust(o) {
    let target = o.target;
    target.style.height = "1px";
    target.style.height = (25 + target.scrollHeight) + "px";
  }

  textAreamouseleave(o) {
    let target = o.target;
    target.style.height = "1px";
    target.style.height = ((target.scrollHeight) - 50 + '%') + "px";
  }

  get f() { return this.addCustomerForm.controls; }

  private formInt() {

    this.addCustomerForm = new FormGroup({
      'company': new FormControl(this.customer.company, [Validators.required]),
      // 'businessType': new FormControl(this.customer.businessType, [Validators.required]),
      'title': new FormControl(this.customer.title, [Validators.required]),
      'contact': new FormControl(this.customer.contact, [Validators.required]),
      'tel': new FormControl(this.customer.tel, [Validators.required]),
      'email': new FormControl(this.customer.email, [Validators.required]),
      'address': new FormControl(this.customer.address, [Validators.required]),
      'discription': new FormControl(this.customer.discription, [Validators.required])
    });
  }

  onSubmit() {

    // this.masseges = [];
    let controls = this.addCustomerForm.controls;
    
    let items = this.valForm.validate(controls,this.customer);
    //if(items['status'] === false) this.masseges.push(items['errors']);
    console.log(items);

    if(items['status']) { 
      //this.send(items); 
    }else{
      this.masseges = items['errors'];
    }

  }

  reset() {
    this.addCustomerForm.reset();
  }
  
  close() {
    this.router.navigate(['../']);
  }

  allTodefault(){

    let controls = this.addCustomerForm.controls;
    
    for(let ii in controls){
      if(controls.hasOwnProperty(ii)){
        if(controls[ii].value !== this.customer[ii]){
          if(this.customer[ii]) controls[ii].setValue(this.customer[ii]);
        }
      }
    }
  }

  send(body){

    let updaterUrl = "http://ethio/customers/" + this.customer["id"];
    console.log(body);
    this.halls.patchData(updaterUrl, body, {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        'Authorization': 'Bearer ' + this.apiKey
      })
    }).pipe(tap(res => console.log(res)))
    .subscribe(evt => {
      
      //console.log(evt);
      let errors = evt['errors'];
      let success = evt['success'];
      let msgOb = {};
      
      if(errors){
        msgOb['errors'] = {};
        for(let ii in errors){

          msgOb['errors'][ii] = {};
          errors[ii].forEach(element => {
            
            msgOb['errors'][ii][ii] = element;
            msgOb['errors'][ii]['type']  = "warning";
          });
        }
       this.masseges = msgOb['errors'];
      }
      if(success){
        msgOb['success'] = {};
        for(let ii in success){

          msgOb['success'][ii] = {};
          msgOb['success'][ii][ii]  = success[ii];
          msgOb['success'][ii]['type']  = "success";
        }
       this.masseges = msgOb['success'];
      }
    }, (err) => {
      
      console.log(err);
      if (err["status"] === 401) {
        console.log("Errrorss 401");
        this.router.navigate(["/login"]);
        //this.errors.push(err["error"]["message"]);
        //this.errors.push(err["status"]+" נא להחתחבר מחדש");
      }
    });
  }
}