import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule, Routes } from '@angular/router';

import { PrintsComponent } from './prints.component';
import { PrintComponent } from './print/print.component';
import { PrintMediaComponent } from './print-media/print-media.component';
import { PrintAboutComponent } from './print-about/print-about.component';
import { PrintEditComponent } from './print-edit/print-edit.component';
import { PrintAllEditComponent } from './print-edit/all-edit/print-all-edit.component';
import { PrintBasicEditComponent } from './print-edit/basic-edit/print-basic-edit.component';
import { PrintMediaEditComponent } from './print-edit/media-edit/print-media-edit.component';

const printinsRoutes: Routes = [

  { path: "prints", component: PrintsComponent, /* resolve: { halls: HallResolver }, */
    children: [
      {
        path: ":id", component: PrintComponent, children: [
          { path: "media", component: PrintMediaComponent },
          { path: 'about', component: PrintAboutComponent },
          {
             path: "edit", component: PrintEditComponent ,
             children: [
              { path: "all", component: PrintAllEditComponent },
              { path: "gallery", component: PrintMediaEditComponent },
              { path: "basic", component: PrintBasicEditComponent }
             ]
          },
        ]}
    ],
  }

];


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(printinsRoutes)
  ],
  exports: [RouterModule]
})
export class PrintsRoutingModule { }
