import { Injectable, Inject } from '@angular/core';
import { Resolve, Router } from '@angular/router';
// import { HallType } from '../../../customers/hall-type';
import { Observable } from 'rxjs';
import { CustomersDataService } from '../../../customers/customers-data-service';
import { map } from 'rxjs/operators';

@Injectable()

export class PhotographersResolver implements Resolve<any> {

    constructor(private halls: CustomersDataService,private router:Router){}

    resolve(): Observable<any> | Promise<any[]> | any[] | any {
        return this.halls.getCustomers('photographers');
    }
}