import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule, Routes } from '@angular/router';

import { TransportsComponent } from './transports.component';
import { TransportComponent } from './transport/transport.component';
import { TransportMediaComponent } from './transport-media/transport-media.component';
import { TransportAboutComponent } from './transport-about/transport-about.component';
import { TransportEditComponent } from './transport-edit/transport-edit.component';
import { TransportAllEditComponent } from './transport-edit/all-edit/transport-all-edit.component';
import { TransportBasicEditComponent } from './transport-edit/basic-edit/transport-basic-edit.component';
import { TransportMediaEditComponent } from './transport-edit/media-edit/transport-media-edit.component';

const transportsRoutes: Routes = [

  { path: "transports", component: TransportsComponent, /* resolve: { halls: HallResolver }, */
    children: [
      {
        path: ":id", component: TransportComponent, children: [
          { path: "media", component: TransportMediaComponent },
          { path: 'about', component: TransportAboutComponent },
          {
             path: "edit", component: TransportEditComponent ,
             children: [
              { path: "all", component: TransportAllEditComponent },
              { path: "gallery", component: TransportMediaEditComponent },
              { path: "basic", component: TransportBasicEditComponent }
             ]
          },
        ]}
    ],
  }

];


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(transportsRoutes)
  ],
  exports: [RouterModule]
})
export class TransportsRoutingModule { }
