import { Component, OnInit } from '@angular/core';
import { HallType } from '../../../../customers/hall-type';
import { ActivatedRoute } from '@angular/router';
import { CustomersDataService } from '../../../../customers/customers-data-service';
import { Observable, of } from 'rxjs';
import { first } from 'rxjs/operators';
import { HttpService } from '../../../../http.service';
declare var $: any;

@Component({
  selector: 'app-photographer-media',
  templateUrl: './photographer-media.component.html',
  styleUrls: ['./photographer-media.component.css']
})
export class PhotographerMediaComponent implements OnInit {
  customer: Observable<HallType>;;
  galleries:any;
  videos:any;

  constructor(private route: ActivatedRoute, private hall: CustomersDataService, private http: HttpService) { }

 
  ngOnInit() {
  
    this.hall.customerObsever.pipe(first()).subscribe(data =>{
      
      let co = data['customer'];
      let gal = data['gallery'];
      
      this.customer = of(co);
      this.galleries = JSON.parse(gal['image']);
      this.videos = JSON.parse(gal['video']);
      
      $('.carousel').carousel();
    });

  }
  galActiveItem(ii){
    
    $(".carousel-indicators")[0].children[ii].click();
  }

  vidActiveItem(ii){
    
    //$(".carousel-indicators")[0].children[ii].click();

  }
}
