import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule, Routes } from '@angular/router';

import { SalonsComponent } from './salons.component';
import { SalonComponent } from './salon/salon.component';
import { SalonMediaComponent } from './salon-media/salon-media.component';
import { SalonAboutComponent } from './salon-about/salon-about.component';
import { SalonEditComponent } from './salon-edit/salon-edit.component';
import { SalonAllEditComponent } from './salon-edit/all-edit/salon-all-edit.component';
import { SalonBasicEditComponent } from './salon-edit/basic-edit/salon-basic-edit.component';

const salonsRoutes: Routes = [

  { path: "salons", component: SalonsComponent, /* resolve: { halls: HallResolver }, */
    children: [
      {
        path: ":id", component: SalonComponent, children: [
          { path: "media", component: SalonMediaComponent },
          { path: 'about', component: SalonAboutComponent },
          {
             path: "edit", component: SalonEditComponent ,
             children: [
              { path: "all", component: SalonAllEditComponent },
              { path: "gallery", component: SalonMediaComponent },
              { path: "basic", component: SalonBasicEditComponent }
             ]
          },
        ]}
    ],
  }

];


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(salonsRoutes)
  ],
  exports: [RouterModule]
})
export class SalonsRoutingModule { }
