import { Injectable } from '@angular/core';
import { isArray } from 'util';

@Injectable({
  providedIn: 'root'
})
export class FormProccesorService {

  constructor() { }


  validate(inputControllers, customer, key?) {

    let truly = true;
    let err = {};
    let success: any = {};

    if (key) {
      let inputs = this.checkInputs(inputControllers, key, customer);
      let inp = inputs['input'];
      let status = inputs['status'];

      success[key] = inp[key];
      err[key] = [inp];

      return {
        'success': status ? success : false,
        'errors': status ? success : err,
        'status': status
      };

    } else {

      let stat: boolean = false;
      for (let ii in inputControllers) {

        let input = this.checkInputs(inputControllers[ii], ii, customer);
        let inputs = input['input'];
        if (!input.status) { truly = false; }

        for (let ii in inputs) {
          if (ii != 'type') err[ii] = [inputs];
        }

        if (input.status) {
          stat = true;
          for (let idx in inputs) {
            if (idx != 'type') success[idx] = inputs[idx];
          }
        }
      }//END FOR IN LOOP

      return {
        'success': stat ? success : stat,
        'errors': err,
        'status': truly
      };
    }


  }

  checkInputs(input, ii, customer) {
    let gnKey = {
      company: "שם חברה",
      contact: "איש קשר",
      email: "אימייל",
      tel: "טלפון",
      address: "כתובת",
      title: "תיאור או כותרת",
      discription: "אודות החברה"
    };

    let inputs = {};
    let isTrue = true;
    let transformHebKey = {};
    if (!gnKey[ii]) {
      return {
        'status': false,
        'input': transformHebKey[ii] = "שגיעה"
      }//isTrue?inputs:transformHebKey
    }
    if (input.touched && input.valid && input.dirty && customer[ii] != input.value) {
      inputs[ii] = input.value;
      inputs['type'] = "success";
    } else if (customer[ii] === input.value) {
      //console.log(gnKey[ii]);
      isTrue = false;
      transformHebKey['type'] = "warning";
      transformHebKey[ii] = gnKey[ii] + ": לא ביצעת שינויים כל שהם.";
    } else {
      //onsole.log(gnKey[ii]);
      isTrue = false;
      transformHebKey['type'] = "danger";
      transformHebKey[ii] = gnKey[ii] + ": לא תקף.";
    }
    return {
      'status': isTrue,
      'input': isTrue ? inputs : transformHebKey
    }//isTrue?inputs:transformHebKey;
  }

  getMassages(evt) {
    let errors = evt['errors'];
    let success = evt['success'];

    if (errors) {
      console.log(errors);
      let messagesErrors = {};
      messagesErrors['errors'] = [];
      let ob = {};
      for (let ii in errors) {

        if (isArray(errors[ii]) && errors[ii].length > 0) {

          messagesErrors[ii] = errors[ii];
          ob[ii] = {};
          errors[ii].forEach(element => {

            element['type'] = "danger";
            messagesErrors['errors'].push(element);
          });
          ob[ii] = messagesErrors['errors'];
        }
      }
      return ob;
    }
    if (success) {
      console.log(success);
      let messagesSuccess = {};
      messagesSuccess['success'] = [];
      let ob = {};
      for (let ii in success) {

        if (isArray(success[ii]) && success[ii].length > 0) {

          messagesSuccess[ii] = success[ii];
          ob[ii] = {};
          success[ii].forEach(element => {

            element['type'] = "success";

            messagesSuccess['success'].push(element);
          });
          ob[ii] = messagesSuccess['success'];
        }
      }
      return ob;
    }
  }
  resetMessages(){
    let timeOut:any = false;
    
    let promise = new Promise((res,rej) => {
      let dissmise: boolean = true;
      if(timeOut) {clearTimeout(timeOut); dissmise = false;}
        console.log(timeOut);
        
        timeOut = setTimeout(()=>{
          return dissmise?res([]):null;
        },5000);
    });
    // console.log(timeOut);
    return promise;
  }
}
