import { Component, OnInit, Input } from '@angular/core';
import { CustomersDataService } from '../../../../customers/customers-data-service';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable, of } from 'rxjs';
import { HallType } from '../../../../customers/hall-type';
import { first } from 'rxjs/operators';

@Component({
  selector: 'app-photographer-about',
  templateUrl: './photographer-about.component.html',
  styleUrls: ['./photographer-about.component.css']
})
export class PhotographerAboutComponent implements OnInit {

  customer$: Observable<{}>;
  constructor(private hall: CustomersDataService) { }

  ngOnInit() {
    
    this.hall.customerObsever.pipe(first()).subscribe(
      (dt) => {
        this.customer$ = of(dt['customer']);
      });
  
  }
}
