import { Injectable } from '@angular/core';
import { HallType } from './hall-type';
import { BehaviorSubject, of, Subject, Observable, AsyncSubject, ReplaySubject } from 'rxjs';
import { tap, single, map, find, filter, first, pluck, take } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class CustomersDataService {

    private customers:Promise<any>;
    private costs: any = false;

    /**** costmumer */
    private customer: any = new BehaviorSubject(1);
    public customerObsever = this.customer.asObservable();

    /**** costmumers media prop */
    private galleries: any;
    private emailPatteren: string = '^[a-z]+[a-zA-Z_\\d]*@[A-Za-z]{2,10}\.[A-Za-z]{2,3}(?:\.?[a-z]{2})?$';
    public intendedUrl: string;

    constructor(private http: HttpClient) {

        this.customers = this.http.get("http://ethio:8080/api/customers")
        .pipe(
            tap(res => {
                console.log(res);
                this.costs = res['customers'];
                this.galleries = res['galleries'];
            })
            
        ).toPromise().catch(this.handleError);
    }
    public getCustomers(type) {
        if(this.costs){ return this.costs[type];}

        return this.customers.then(
            (res) => {
                let data = res['customers'];
                return data && data[type]?data[type]:[];
            },
            (res) => {
                console.log("No data!");
            }
        );
    }
    handleError(errors){
        console.log("YOu have problem with your sever!");
        console.log(errors['message']);
        
    }

    customerEmit(customer) {
        this.customer.next(customer);
    }

    getGallery(id) {
        //return this.findItems(this.galleries, id);
        //if(this.costs){ return this.costs.filter((items) => {return items.customer_id == id;});}

        return this.galleries.find((items) => {return items.customer_id == id;});
    }

    getVideo(id) {
        // return this.videos.filter((items) => {return items.customer_id == id;});
        // return this.findItems(this.videos, id);
    }

    storeData(postUrl, inputData, opt) {
        return this.http.post(postUrl, inputData, opt);
    }

    patchData(postUrl, inputData, opt) {
        
        return this.http.post(postUrl, inputData, opt);
    }

    putData(postUrl, inputData, opt) {
        return this.http.put(postUrl, inputData, opt);
    }

    public getById(customerName: any) {
        // let customers = this.costs? this.costs: this.customers;
        let isNumric = (! isNaN(parseFloat(customerName)) && isFinite(customerName));
        let type = this.intendedUrl? this.intendedUrl:decodeURIComponent(location.pathname).split('/')[1];
        type = (type === "app")?decodeURIComponent(location.pathname).split('/')[2]:type;
        //  console.log(type);
                
        let prop: any = "company";
        if (isNumric) {
            prop = "id";
            customerName = +customerName;
        }
        if(! isNumric){
            let regex = customerName.match(this.emailPatteren);
            if(regex && regex[0]) prop = "email";
        }
        if(this.costs){
            if(type == '/join'){
                let concated = this.concatCustomers(this.costs);
                let isCustomer = concated?concated.find((items) => {return items['customer'][prop] == customerName;}):false;
                (typeof isCustomer === "object")? this.customer.next(isCustomer): this.customer.next({});
                return this.customerObsever;
            }
            
            let costmumerType = this.costs[type]?this.costs[type]:false;
           
            let co = costmumerType?costmumerType.find((items) => {return items['customer'][prop] == customerName;}):false;
            (typeof co === "object")? this.customer.next(co): this.customer.next({});
            return this.customerObsever;
        } 
        this.customers.then(
                (data) => {
                    let co = data['customers']? data['customers'][type]:false;
                   
                    if(type == '/join'){
                        let concated = co?this.concatCustomers(co):false;
                        let isCustomer = concated?concated.find((items) => {return items['customer'][prop] == customerName;}):false;
                        (typeof isCustomer === "object")? this.customer.next(isCustomer): this.customer.next({});
                        return;
                    }
                    co = co && co? co.find((elem) => { return elem['customer'][prop] == customerName }):false;
                    (typeof co === "object")?this.customer.next(co):this.customer.next({});  
                }
            );
        return this.customerObsever;
    }

    concatCustomers(costmumers){
        let cos = [];
        for(let ii in costmumers){
            let cost = costmumers[ii];
            if(cos.length === 0){
                cos = cost;
            }else{
                cos = cos.concat(cost);
            }
            //console.log(cost);
            
        }
        return cos;
    }
   /*  public getCustomer(param?) {
       //console.log((param && this.customer.value === 1));
        //return (param && this.customer.value === 1) ? this.getById(param) : this.customerObsever;
    } */
    customerPromis(customerUriRecourse) {
        
        let promisse = new Promise((resolve, reject) => {

            this.getById(customerUriRecourse)
            .pipe(first(param => typeof param === "object"))
            .subscribe(
                (customer) => {
                    let co = customer['customer'];
                    if (typeof co === "object" && co['email']) {
                        resolve(co);
                    } else {
                        reject(false);
                    }
                });
        });
        return promisse;
    }

    findItems(obj, id) {

        let arrItem = [];
        let gals = obj['value'];
        let idx = 0;
        for (let ii of gals) {

            if (ii.customer_id === id) {
                //this.customer.next(gals[ii]);
                arrItem[idx++] = ii;
            }
        }
        return arrItem;
    }

    finCustomer(elem,prop,costName){
        return elem[prop] == costName;
    } 
}

