import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, Router, RouterStateSnapshot, CanActivateChild, ActivatedRoute, Params, Data } from '@angular/router';
import { HttpService } from './http.service';
import { Observable, of } from 'rxjs';
import { CustomersDataService } from './customers/customers-data-service';
import { map, last, filter, first, skip, tap, find } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class RouteGuardService implements CanActivate, CanActivateChild {

  private autUser = {};

  constructor(private http: HttpService, private halls: CustomersDataService, private router: Router, private active: ActivatedRoute) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    /****** 
     * if costumr recourse is true else ->> return false and redirect to main path recourse
     * then check if user authenticated is true else ->> rediract to recourse/customer/media and ask to Log-in
     * then check if customer recourse blong to user authenticated true else ->> rediract to recourse/customer/media 
     * then user is owner and return true
    ******/

    /** lets defined somes props **/
    let intendedUrl: string = decodeURIComponent(state.url);
    let customerUriRecourse =  route.parent.params.id;

    this.autUser = this.http.authUser;
    let uEmail = this.autUser ? this.autUser['email'] : false;
    let join = intendedUrl.indexOf('/join');
    let reg = intendedUrl.indexOf('/register');
    console.log(intendedUrl);
    
    /** if our uri is /join, lets check if auth user is already our customer member and let him access join page if false **/
    if(join >= 0 && uEmail) {
      this.halls.intendedUrl = intendedUrl;
      return this.userAlreadyCustomer(uEmail).then((res)=> {
        console.log(intendedUrl);
        
          return (! res)? true: this.goTo(intendedUrl);
        });
    }
    /**** so if we have auth user lats check if we have customer and let access intended page if user are owner ****/
    if(uEmail) {return this.checkCustomer(customerUriRecourse,uEmail,intendedUrl);};

    return this.http.userPromise()
      .then((response) => {
          /*** attach auth user props ***/
          uEmail = response['email'];
          this.autUser = response && response['email'] ? response : null;

          /** if our uri is /join, lets check if auth user is already our customer member and let him access join page if false **/
          if(join >= 0 && response['email']) {
            this.halls.intendedUrl = intendedUrl;
            return this.userAlreadyCustomer(uEmail).then((res)=> {
              return (! res)? true: this.goTo(intendedUrl);
            });
          }
          /**** so if we have auth user lats check if we have customer and let access intended page if user are owner ****/
          return this.checkCustomer(customerUriRecourse,uEmail,intendedUrl);

        },
        (reject) => {
          /****** if we in /join page and we have no user. lets get user to log in ******/
          if(join >= 0 && ! uEmail){
            this.halls.intendedUrl = intendedUrl;
            this.http.allowLogIn.next(true);
            return this.goTo(intendedUrl);
          }
          /****** we have no user. lets check customer uri recourse ******/
          if(! customerUriRecourse) return this.goTo(intendedUrl);
          /**** lats check if we have customer and navigate recourse if true and let user log in ****/
          return this.checkCustomer(customerUriRecourse,uEmail,intendedUrl);
        });
  }

  goTo(intendedUrl){
    let goTo = (intendedUrl.indexOf('/halls-events') >= 0) ? "/halls-events" : "/";
    this.router.navigate([goTo]);
    return false;
  }

  checkCustomer(customerUriRecourse,uEmail,intendedUrl){
    return this.halls.customerPromis(customerUriRecourse)
        .then((res) => {
          
          /****** if we have user and customer. lets check if auth user is owen recourse if true give auth user access ******/
          if (uEmail === res['email']) { return true; }

          /****** if auth user is not own recourse check one more time if we have auth user if false navigate him to recourse/media with posibility to log in ******/
          if(! uEmail) {
            this.http.allowLogIn.next(true);
            this.http.IntendedUri = intendedUrl;
          }
          /****** redirect user to recourse/media  ******/
          let goToMedia = "/" + res['businessType'] + "/" + customerUriRecourse + "/media";
          this.router.navigate([goToMedia]);
          return false;
        },
        (rej) => {
          
          if(! uEmail) {console.log("No customer recourse and No auth User");}

          /****** redirect user to main page or main recourse ******/
          return this.goTo(intendedUrl);
        });
  }

  userAlreadyCustomer(param){
    return this.halls.customerPromis(param).then((res) => { return res;},(rej) =>{ return rej;});
  }
  canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    return this.canActivate(route, state);
  }

}


