import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Router, ActivatedRoute, RouterStateSnapshot } from '@angular/router';
import { HttpService } from '../http.service';
import { Observable, of } from 'rxjs';
import { first } from 'rxjs/operators';
declare let $:any;
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
 /*********Log-in props *********/
 logInform: FormGroup;
  phoneNum: any = '^((?=(02|03|04|08|09))[0-9]{2}[0-9]{3}[0-9]{4}|(?=(05|170|180))[0-9]{3}[0-9]{3}[0-9]{4})';
  emailPatt: string = '^[a-z]+[a-zA-Z_\\d]*@[A-Za-z]{2,10}\.[A-Za-z]{2,3}(?:\.?[a-z]{2})?$';
  passwordPatt: string = '^\\w{6,}$';
  private url:string;

 /*********Header props *********/
  allowLog: Observable<boolean>;
  auth: Observable<boolean>;

  constructor(private http: HttpService, private router: Router, private route: ActivatedRoute) {}

  ngOnInit() {
    this.url = decodeURIComponent(location.pathname);
    this.http.isAuthenticeted();
    this.auth = this.http.isLogedIn;
    this.http.allowLogIn.subscribe((allow) => { if(allow) $('#myFormModel').modal();this.url = decodeURIComponent(location.pathname);});
    this.formInit();
    
  }

  logIn(){
    this.url = decodeURIComponent(location.pathname);
    console.log(this.url);
    $('#myFormModel').modal();
  } 

  logOut(){
    this.url = decodeURIComponent(location.pathname);

    this.http.logOut().subscribe(evt =>{

    });
    let splitUrl:any = ( this.url.indexOf('halls-events') >= 0)?  this.url.split("/"): false;
        splitUrl = (splitUrl && (splitUrl[1] && splitUrl[2])) ?  splitUrl[1]+"/"+splitUrl[2]+"/media" :(splitUrl && splitUrl[1])? splitUrl[1]: "/";
        this.router.navigate([splitUrl], { relativeTo: this.route });
        // location.reload();
  }
   
  onSubmit() {
    this.url = decodeURIComponent(location.pathname);
    if (this.logInform.valid) {
      
      this.http.logIn(this.logInform.value).
        subscribe(evt => {
          
          let redirectUrl = this.http.IntendedUri? this.http.IntendedUri: this.url? this.url: "/";
          console.log(redirectUrl);
          
          this.router.navigate([redirectUrl]);
          $('.close').click();
          // location.reload();
        },
        (err) => {
          // this.http.nextIslogged(false);
          console.log(err);
        });
    }
  }

  register(){
    $('.close').click(); 
    this.router.navigate(['/register']);
  }
  
  get f() { return this.logInform.controls; }

  private formInit(){
   
    this.logInform = new FormGroup({
      "name": new FormControl(null),
      'logInEmail': new FormControl(null),
      'password': new FormControl(null)
    });
    

    
   /*  let thiz = this;
    model.on('hidden.bs.modal', function(e){
    
    thiz.router.navigate(["/"]);
    return e.preventDefault() ;
    }); */
  }
}
