import { Component, OnInit, OnDestroy, EventEmitter } from '@angular/core';
// import { Location } from '@angular/common';
// import { PagesService } from '../../../pages-service';
import { Router, RoutesRecognized, ActivatedRoute, Data } from '@angular/router';
import { map, filter, tap } from 'rxjs/operators';
import { CustomersDataService } from '../../../customers/customers-data-service';
import { HallType } from '../../../customers/hall-type';
import { Observable, of } from 'rxjs';

@Component({
  selector: 'app-photographers',
  templateUrl: './photographers.component.html',
  styleUrls: ['./photographers.component.css']
})
export class PhotographersComponent implements OnInit {

  path: boolean = true;
  urlUnsubscribe: any;
  photoProps: Observable<HallType[]>;

  constructor(private router: Router, private route: ActivatedRoute,private halls: CustomersDataService ) {}

  ngOnInit() {
    
    this.route.data.subscribe(data => {
      // console.log(data);
      
       this.photoProps =  data['photographers']? of(data['photographers']):null;
       
    });

    this.getCurrentUrl();
  }

  private getCurrentUrl(){

    let urlSliced = decodeURIComponent(window.location.pathname).slice(1);
    let urlPath = (urlSliced === 'photographers' || urlSliced === 'app/photographers')? true : false;

      this.urlUnsubscribe = this.router.events.pipe(
        filter((evt) => evt instanceof RoutesRecognized),
        map(_evt => decodeURIComponent(_evt["url"]).slice(1)),
      ).subscribe(evt => {
        this.path = (evt === 'photographers' || evt === 'app/photographers') ? true : false;
      });
    this.path = urlPath;
  }

  onSelectedLink(customer:HallType){
    //this.halls.customerEmit(customer);
  }

  ngOnDestroy() {
    this.urlUnsubscribe.unsubscribe();
  }

}
