import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { of, BehaviorSubject, Observable, AsyncSubject } from 'rxjs';
import { tap, delay, map, find, filter, first } from 'rxjs/operators';
import { Router } from '@angular/router';



@Injectable({
  providedIn: 'root'
})
export class HttpService {

  private headersOpt: Object = {
    headers: new HttpHeaders({
      'Accept': 'application/json',
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };
  private apiKey: any;
  private logged: any = new BehaviorSubject(false);
  private user: BehaviorSubject<{ name: string, id: number, email: string } | boolean> = new BehaviorSubject(false);

  public allowLogIn = new BehaviorSubject(false);
  public IntendedUri: string;
  public authUser: { name: string, id: number, email: string };

  public isLogedIn: Observable<boolean> = this.logged.asObservable();
  public userObs: Observable<{ name: string, id: number, email: string  }| boolean> = this.user.asObservable();

  constructor(private http: HttpClient, private router: Router) { }

  public logIn(credential) {

    // 'Content-Type': 'application/json'
    const theUrl = "http://ethio:8080/api/login";

    let body = new HttpParams()
      .set('name', credential['name'])
      .set('email', credential['logInEmail'])
      .set('password', credential['password']);

    return this.http.post(theUrl, body, this.headersOpt)
      .pipe(
        first(),
        tap(res => {
          const apiKey = res['access_token'];
          const userKey = "user_key";
          this.apiKey = apiKey;
          window.sessionStorage.setItem(userKey, apiKey);
          this.logged.next(true);
          this.allowLogIn.next(false);
          
          this.user.next(res["user"]);
        })
      );
  }

  userPromise() {
    const theUrl = "http://ethio:8080/api/isauth";
    let sSK = window.sessionStorage.getItem('user_key');
    this.apiKey = this.apiKey ? this.apiKey : sSK;
    let token = new HttpParams().set('token', this.apiKey);
    
    let promisse = new Promise((response, reject) => {
      this.http.post(theUrl, token, this.headersOpt).
      pipe(first()).subscribe((user) => {
        
          if (user['status']) {
            
            this.user.next(user["user"]);
            this.logged.next(true);
            return response(user["user"]);
          } else {
            this.logged.next(false);
            this.user.next(false);
            return reject("user isnt log in");
          }
        }
      );
    });
    return promisse;
  }

  public logOut() {

    console.log("logout called");
    let token = new HttpParams().set('token', this.apiKey);
    return this.http.post("http://ethio:8080/api/logout", token, this.headersOpt)
      .pipe(
        tap(msg => {

          console.log(msg);
          this.logged.next(false);
          window.sessionStorage.removeItem('user_key');
          this.apiKey = false;
          this.user.next(false);

        }, (err) => {

          console.log(err);
          this.logged.next(false);
          window.sessionStorage.removeItem('user_key');
          this.apiKey = false;
          this.user.next(false);
        })
      );
  }

  store(credential) {

    // console.log(credential);
    const theUrl = "http://ethio:8080/api/store";
    let body = new HttpParams()
      .set('name', credential['name'])
      .set('email', credential['email'])
      .set('password', credential['password'])
      .set('passwordConfirm', credential['passwordConfirm'])
      .set('city', credential['city'])
      .set('area', credential['area'])
      .set('tel', credential['tel'])
      .set('about', credential['about']);

    return this.http.post(theUrl, body, this.headersOpt)
      .pipe(
        tap(api_key => {
          console.log(api_key);
          if (api_key['access_token']) this.logged.next(true);
          window.sessionStorage.setItem('user_key', api_key['access_token']);
          this.apiKey = api_key['access_token'];
          this.user.next(api_key["user"]);

        })
      );
  }

  isAuthenticeted() {

    const theUrl = "http://ethio:8080/api/isauth";
    let sSK = window.sessionStorage.getItem('user_key');
    this.apiKey = this.apiKey ? this.apiKey : sSK;
    let token = new HttpParams().set('token', this.apiKey);
    
    this.http.post(theUrl, token, this.headersOpt).pipe(first())
      .subscribe(
        (res) => {
          
          if (res['status']) {
            this.logged.next(true);
            this.user.next(res["user"]);
          } else {
            this.logged.next(false);
            window.sessionStorage.removeItem('user_key');
            this.user.next(false);
          }
        }
      );
    return this.isLogedIn;
  }

  isAuth() {
    return this.isLogedIn;
  }

  getApiKey() {
    return this.apiKey;
  }

  nextIslogged(param) {
    this.logged.next(param);
  }

}