import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule, Routes } from '@angular/router';

import { KysesComponent } from './kyses.component';
import { KyseComponent } from './kyse/kyse.component';
import { KyseMediaComponent } from './kyse-media/kyse-media.component';
import { KyseAboutComponent } from './Kyse-about/kyse-about.component';
import { KyseEditComponent } from './kyse-edit/kyse-edit.component';
import { KyseAllEditComponent } from './Kyse-edit/all-edit/kyse-all-edit.component';
import { KyseBasicEditComponent } from './Kyse-edit/basic-edit/kyse-basic-edit.component';

const kysesRoutes: Routes = [

  { path: "kyses", component: KysesComponent, /* resolve: { halls: HallResolver }, */
    children: [
      {
        path: ":id", component: KyseComponent, children: [
          { path: "media", component: KyseMediaComponent },
          { path: 'about', component: KyseAboutComponent },
          {
             path: "edit", component: KyseEditComponent ,
             children: [
              { path: "all", component: KyseAllEditComponent },
              { path: "gallery", component: KyseMediaComponent },
              { path: "basic", component: KyseBasicEditComponent }
             ]
          },
        ]}
    ],
  }

];


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(kysesRoutes)
  ],
  exports: [RouterModule]
})
export class KysesRoutingModule { }
